@import "../../styles/AppDesign.scss";

.sec-tour{
    position: relative;
    // min-height: 150vh;
    display:flex;
    align-items: center;
    // min-height: 100vh;
    width: 100%;
    z-index: 600;
    top:0;
    left:0;
    @include comingSoonCursor();
    @include secPadding();
}
.sec-tour__inner{
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-tour__title{
    @include secTitle();
}
.sec-tour__desc{
    @include secDesc();
}
