@import "../../styles/AppDesign.scss";

.subs-popup{
    position:fixed;
    width:100%;
    height:100vh;
    background:rgba(#000, 0.5);
    z-index: 2000;
    top:0;
    left:0;
    pointer-events: none;
    opacity: 0;
    transition: .4s;
    &.is-open {
        pointer-events: all;
        opacity: 1;
    }
}
.subs-popup__overlay{
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height:100%;
    z-index: 0;
    font-size: 0;
    background: transparent;
    border:0;
    opacity: 0;
    cursor: pointer;
}
.subs-popup__body{
    position: absolute;
    top:0;
    right:0;
    width: 50%;
    height:100%;
    z-index: 10;
    background:#FFF;
    background-image: url('/img/landing/bg-curve.svg'); 
    overflow:auto;
    transform: translateX(101%);
    transition: .4s;
    box-shadow: -20px 0px 30px 0px rgba(0, 0, 0, 0.75);
    padding: 35px 50px;


    background: $bgWhite;
    @include tabletView(){
        width: 75%;
    }
    @include mobileView(){
        padding: 20px 20px;
        width: 100%;
    }
    &:before {
        content: '';
        display:block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: url('/img/landing/bg-curve-black.svg') no-repeat right top;
        background-size: 200% auto;
        transition: .5s;
        z-index: 0;
        pointer-events: none;
        opacity: 0.15;
    }
    .subs-popup.is-open &{
        transform: translateX(0);
    }
}
.subs-popup__head{
    position: relative;
    margin-bottom:80px;
    z-index: 5;
}
.subs-popup__close{
    display:block;
    $size:30px;
    width:$size;
    height:$size;
    padding:0;
    font-size: 0;
    border: 0;
    background: url('/img/icon/icon-close.svg') transparent no-repeat center center;
    background-size: contain;
    cursor: pointer;
    @include mobileView(){
        $size:20px;
        width:$size;
        height:$size;
    }
}
.subs-popup__form{
    position: relative;
    z-index: 5;
}