@import "../../styles/AppDesign.scss";

.main-layout {
  overflow:auto;
  width:100%;
}
.main-layout__body{
  width:100%;
  min-height: 100vh;
  // background:$main;
  background: $bgWhite;
  &:after {
      content: '';
      display:block;
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100vw;
      background: url('/img/landing/bg-curve-red.svg') no-repeat right top;
      background-size: 100% auto;
      transition: .5s;
      z-index: 550;
      pointer-events: none;
      .after-video &{
        background-image: url('/img/landing/bg-curve.svg'); 
      }
      .color-inversed &{
        background-image: url('/img/landing/bg-curve-white.svg'); 
      }
  }
}
.main-layout__section-spacer{
  display:block;
  @include secSpacer();
}