@import "../../styles/AppDesign.scss";

.sec-journey {
    position: relative;
    min-height: 100vh;
    width: 100%;
    z-index: 500;
    top: 0;
    left: 0;
    @include secPadding();
}
.sec-journey__deco{
    position: absolute;
    width:100%;
    left:0;
    bottom:0;
    transform: translateY(50%);
    pointer-events: none;
}
.sec-journey__bg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 0;
    overflow: hidden;

}

.sec-journey__bg-graphic {
    background: #FFF;
    height: 300vh;
    width: 300vh;
    position: absolute;
    left: 0;
    top: 0;
    transform-origin: left top;
    transform: rotate(90deg);
    transition: .6s;
    z-index: 10;

    .sec-journey.is-active & {
        transform: rotate(0);
    }
}

.sec-journey__inner {
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-journey__title {
    @include secTitle();
}

.sec-journey__desc {
    @include secDesc();
}

.sec-journey__subsec{
    &--create{
        margin-top:120px;
        .sec-journey__title {
            margin-top:0;
            margin-top:0;
        }
    }
    &--history{
        margin-top:180px;
        @include mobileView(){
            margin-top:90px;
        }
    }
}