@import "../../styles/AppDesign.scss";

.sec-gallery{
    position: relative;
    // min-height: 150vh;
    display:flex;
    align-items: center;
    width: 100%;
    z-index: 600;
    top:0;
    left:0;
    @include secPadding();
    @include comingSoonCursor();
}
.sec-gallery__bg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 495;
    overflow: hidden;
    background:$bgWhite;
    transition: 1s;
    opacity:0;
}
.sec-gallery.is-active + .sec-gallery__bg {
    opacity:1   
}
.sec-gallery__inner{
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-gallery__title{
    @include secTitle();
}
.sec-gallery__desc{
    @include secDesc();
    position: relative;
    width:50%;
    left:50%;
    @include tabletView(){
        width:100%;
        left:0;
    }
}
$gutter: 30px;
.sec-gallery__gallery{
    width:100%;
    display:flex;
    margin-right: -$gutter/2;
    margin-left: -$gutter/2;
    padding-bottom:3rem;
    @include tabletView(){
        display:block;
        margin-right: 0;
        margin-left: 0;
        margin-top:45px;
    }
}
.sec-gallery__gallery-item{
    width:50%;
    padding-right: $gutter/2;
    padding-left: $gutter/2;
    display:flex;
    justify-content: flex-end;
    &:nth-child(2){
        justify-content: flex-start;
        margin-top:10rem;
        @include tabletView(){
            margin-top:2rem;
        }
    }
    @include tabletView(){
        width:100%;
        padding-right: 0;
        padding-left: 0;
    }
}
.sec-gallery__gallery-item-body{
    position: relative;
    flex: 0 0 auto;
    width:125%;
    @include tabletView(){
        width:100%;
    }
}
.sec-gallery__gallery-graphic{
    position: relative;
    width:100%;
    background: no-repeat center center;
    background-size: cover;
    
    &:before{
        content:'';
        display:block;
        padding-bottom:75%;
    }
    &:after{
        content:'';
        position: absolute;
        display:block;
        width:100%;
        height:100%;
        left:0;
        bottom:0;
        // background: $main;
        // background: linear-gradient(180deg, rgba(238,53,84,0.10) 0%, $main 67%);
        // .sec-gallery__gallery-item:nth-child(2) &{
        //     background: linear-gradient(180deg, #D8D8D8 25%, rgba(255,255,255,0) 100%);
        // }
    }
}
.sec-gallery__item-desc{
    margin-top:2rem;
    @include sliderCaption();
    @include tabletView(){
        margin-top:3rem;
    }
}