@import "../../styles/AppDesign.scss";

.landing {
    position: relative;
    height: 100vh;
    width: 100%;
    z-index: 500;
    //overflow: hidden;
    top: 0;
    left: 0;
}

.landing__inner {
    position: fixed;
    // background:$main;
    background: $bgWhite;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 15vh;
    opacity: 0;
    @include mobileView(){
        padding-top: 25vh;
    }
    .landing.is-active & {
        .page-ready & {
            opacity: 1;
        }
    }
}

.landing__slogan {
    display: block;
    opacity: 0;
    $initialDelay: .15s;
    $delayInterval: .15s;
    transform: translateY(200%);
    // transition: transform .5s, opacity .7s;
    height: 5.9vw;

    .landing.is-active & {
        .page-ready & {
            // opacity: 1;
            // transform: translateY(0);
            animation:landing-slogan-move .5s forwards;
            &--1 {
                animation-delay: $initialDelay + $delayInterval * 0;
            }
        
            &--2 {
                animation-delay: $initialDelay + $delayInterval * 1;
            }
        
            &--3 {
                animation-delay: $initialDelay + $delayInterval * 2;
            }
        }
    }

    @keyframes landing-slogan-move {
        0% {
            opacity: 0;
            transform: translateY(150%);
        }
        100% {
            opacity: 1;
            transform: translateY(0);
        }
    }


    // &--1 {
    //     animation-delay: $initialDelay + $delayInterval * 0;
    // }

    // &--2 {
    //     animation-delay: $initialDelay + $delayInterval * 1;
    // }

    &--3 {
        margin-top: 3vh;
    }
}