@import "../../styles/AppDesign.scss";

.sec-video{
    position: relative;
    height: 100vh;
    box-sizing: content-box;
    @include secSpacer();
    &+.main-layout__section-spacer{
        padding:0;
    }
    // min-height: 150vh;
    display:flex;
    align-items: center;
    width: 100%;
    z-index: 1100;
    top:0;
    left:0;
}


.sec-video__bg{
    position: fixed;
    top: 0;
    left:0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 550;
    background: #000;
    overflow:hidden;
    transition: 1s;
    opacity:0;
}
.sec-video.is-active + .sec-video__bg{
    opacity:1   
}

.sec-video__inner{
    position: relative;
    z-index: 30;
    top:0;
    height: 100%;
    width: 100%;
    display:flex;
    align-items: center;
    justify-content: center;
}
