.sec-video__video{
    position:relative;
    width: 100%;
    height:auto;
    // max-width:960px;
    transition: .1s;
    &:before{
        position:relative;
        content:'';
        display:block;
        padding-bottom:75%;
    }
    video{
        position: absolute;
        left:0;
        top:0;
        height: 100%;
        width: 100%;
        object-fit: cover;
        // &::-webkit-media-controls-panel {
        //     display: flex !important;
        //     opacity: 1 !important;
        // }
    }
}

.sec-video__process-info{
    position:fixed;
    width:200px;
    height:200px;
    background:rgba(#fff, 0.5);
    color:#000;
    padding:15px;
    z-index: 1000;
    bottom:0;
    right:0;
    word-break: break-all;
}