@mixin globalHelpers() {
    // Global Helpers
    .align-center {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .force-nobreak {
        white-space: nowrap;
    }

    .a11y-hidden {
        @include a11y-hidden();
    }

    .hidden-desktop {
        @include desktopView() {
            display: none !important;
        }
    }

    .hidden-mobile {
        @include mobileView() {
            display: none !important;
        }
    }

    .visible-desktop {
        @include mobileView() {
            display: none !important;
        }
    }

    .visible-mobile {
        @include desktopView() {
            display: none !important;
        }
    }

    .color-main {
        color: $main;
    }
}