@import "../../styles/AppDesign.scss";

.shapeout-text {
    position: relative;
    width: 100%;
}

.shapeout-text__body {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}

.shapeout-text__line {
    position: relative;
    display: block;
    overflow:hidden;
    font-size: 0;
}

.shapeout-text__content {
    position: relative;
    display: inline-block;
    overflow: hidden;

    >* {
        opacity: 0;
        // transform: translateX(100%);
        z-index: 10;
        @include secTitleTypo();
        // opacity: 1;
    }
}

.shapeout-text__shape {
    position: absolute;
    display: block;
    overflow: hidden;
    width: 65vw;
    height: 90%;
    top: 5%;
    transform-origin: center center;
    transform: skewX(25deg) scaleY(1) translate3d(-105%, -20%, 0);
    opacity: 1;
    z-index: 20;
    transition: 0s;

